* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background: linear-gradient(
        rgba(60, 21, 233, 0.541),
        rgba(184, 141, 62, 0.541)
    );
}

.hidden {
    pointer-events: none;
    cursor: default;
    background-color: #ccc !important;
}
